body, html {
  /* text styles */
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  color: #393939;
}

.contact-link {
  font-size: 18px;
  font-weight: 600;
}

.contact-link:before {
  content: "";
  display: inline-block;
  width: 70px;
  height: 70px;
  background-image: url("/img/icons/telegram.svg");
  background-size: cover;
  vertical-align: middle;
}

.main-title {
  color: #fff;
  font-size: 74px;
  font-weight: 700;
  text-transform: capitalize;
}

.wrapper-main-subtitle {
  margin-bottom: 30px;
}

.main-subtitle {
  font-size: 74px;
  font-weight: 500;
}

.wrapper-section-title {
  margin-bottom: 30px;
}

.section-title {
  font-size: 57px;
  font-weight: 700;
}

.section-title__first-part--one {
  color: #00BDED;
}
.section-title__first-part--second {
  color: #FF577F;
}
.section-title__first-part--tree {
  color: #fff;
}

.section-small-title__first-part--one {
  color: #00BDED;
}

.section-small-title {
  font-size: 37px;
  font-weight: 700;
}

.context-btn {
  display: inline-block;
  border-radius: 114px;
  padding-left: 94px;
  padding-right: 94px;
  height: 70px;
  line-height: 70px;
  font-weight: 700;
  background-color: #fff;
  box-shadow: 5px 10px 40px 8px rgba(255, 255, 255, 0.7), inset 0 0 10px 5px rgba(233, 229, 229, 0.7);
}

.author {
  width: 680px;
  z-index: 3;
}

.author__description {
  margin-bottom: 40px;
  font-size: 27px;
  line-height: 1.3;
}

.avatar__photo {
  width: 630px;
  height: 650px;
  position: relative;
}
.avatar__photo--preview::before {
  content: "";
  position: absolute;
  bottom: -30%;
  right: 0;
  z-index: 2;
  display: inline-block;
  width: 300px;
  height: 600px;
  background-image: url("/img/main-page/preview/telephone-box.png");
  background-size: contain;
  background-repeat: no-repeat;
  transform: rotate(30deg);
  filter: drop-shadow(0 0 10px rgb(180, 50, 50));
}
.avatar__photo--preview::after {
  content: "";
  position: absolute;
  bottom: -20%;
  left: -15%;
  display: inline-block;
  width: 300px;
  height: 600px;
  background-image: url("/img/main-page/preview/big-ben.png");
  background-size: cover;
  background-repeat: no-repeat;
  transform: rotate(-25deg);
  filter: drop-shadow(0 0 10px rgb(180, 135, 50));
}

.context-link {
  color: #00BDED !important;
  font-size: 20px;
  line-height: 1.08;
  text-decoration: underline !important;
  text-underline-offset: 5px;
}

.header {
  padding-top: 20px;
  padding-bottom: 20px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  box-shadow: 4px 0px 6px 6px rgba(255, 255, 255, 0.7);
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(5px);
}

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__menu-list {
  display: flex;
  column-gap: 60px;
  font-size: 18px;
  font-weight: 500;
}

.preview {
  padding-top: 300px;
  padding-bottom: 60px;
  background: -webkit-linear-gradient(107deg, #ffffff, #88addd, #ac9ce8); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(107deg, #ffffff, #88addd, #ac9ce8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 0% 0% 50% 50%/0% 0% 20% 20%;
}

.preview__content {
  display: flex;
  column-gap: 60px;
  align-items: center;
  justify-content: center;
}
.preview__content > * {
  flex-shrink: 0;
}

.about-us {
  padding-top: 200px;
  padding-bottom: 200px;
}

.about-us__item {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 60px;
}
.about-us__item:nth-child(even) {
  flex-direction: row-reverse;
}
.about-us__item:not(:last-child) {
  margin-bottom: 60px;
}

.about-us__info {
  width: 601px;
}

.about-us__description {
  color: #393939;
  font-size: 18px;
  line-height: 1.44;
  margin-bottom: 30px;
}
.about-us__description p:not(:last-child) {
  margin-bottom: 15px;
}

.products {
  padding-top: 200px;
  padding-bottom: 200px;
  background: -webkit-linear-gradient(107deg, #ffffff, #88addd, #ac9ce8); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(107deg, #ffffff, #88addd, #ac9ce8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 50% 50% 50% 50%/20% 20% 20% 20%;
}